/* bold - gilroy */
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local(""), url("../fonts/gilroy/gilroy-bold.woff2") format("woff2"), url("../fonts/gilroy/gilroy-bold.woff") format("woff"); }

/* medium - gilroy */
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local(""), url("../fonts/gilroy/gilroy-medium.woff2") format("woff2"), url("../fonts/gilroy/gilroy-medium.woff") format("woff"); }

:root {
  --content-width: 943px;
  --gutter: 15px;
  --mob-padding: 30px; }

.container {
  max-width: calc(var(--content-width));
  padding: 0 var(--mob-padding);
  margin: 0 auto; }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1); }

* {
  box-sizing: border-box; }

html {
  font-family: "Gilroy";
  font-weight: 500; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

img {
  display: block;
  max-width: 100%; }

.scroll-hidden {
  overflow: hidden; }

.title {
  text-align: center;
  font-size: 34px;
  line-height: 42.09px;
  font-weight: bold; }

@media (max-width: 992px) {
  .header__menu-wrap {
    opacity: 0;
    overflow: hidden;
    z-index: -99;
    transition: 0.3s;
    position: relative; } }

@media (max-width: 992px) {
  .header__menu-wrap_active {
    position: static;
    height: auto;
    overflow: visible;
    opacity: 1;
    z-index: 998; } }

@media (max-width: 992px) {
  .header__menu-wrap .header__nav {
    padding-top: 65px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #ffffff; } }

.header__menu-wrap .header__nav_active {
  width: 100%;
  height: 100%;
  overflow: scroll; }

.header__menu-wrap .header__menu {
  margin: 0; }
  .header__menu-wrap .header__menu-item {
    list-style-type: none; }
    @media (max-width: 992px) {
      .header__menu-wrap .header__menu-item {
        margin-bottom: 65px; } }
  .header__menu-wrap .header__menu-link {
    display: block;
    text-decoration: none;
    color: #828282;
    font-weight: bold;
    font-size: 34px; }
    .header__menu-wrap .header__menu-link_active {
      color: #000000; }

.header__burger {
  display: none; }
  @media (max-width: 992px) {
    .header__burger {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 20px;
      width: 33px;
      cursor: pointer;
      z-index: 999;
      user-select: none;
      transition: all 0.5s ease-in-out; } }

.header__line {
  width: 100%;
  height: 4px;
  background-color: #000000;
  transition: all 0.5s ease; }
  .header__line::after, .header__line::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #000000;
    transition: all 0.5s ease; }
  .header__line::after {
    transform: translateY(-8px); }
  .header__line::before {
    transform: translateY(8px); }

.header__burger-active .header__line {
  background-color: transparent; }
  .header__burger-active .header__line::before {
    transform: rotate(45deg); }
  .header__burger-active .header__line::after {
    transform: rotate(-45deg); }

@media (max-width: 376px) {
  .header .header__inner {
    padding-top: 65px;
    padding-bottom: 80px; } }

.header__content-wrap {
  display: flex;
  justify-content: space-between; }

.header__content {
  display: flex;
  flex-direction: column; }

.header__title {
  margin-top: 0;
  font-size: 47px;
  font-weight: bold; }

.header__subtitle {
  font-size: 18px;
  font-weight: 500; }

.header__langs-wrap {
  position: relative;
  display: flex;
  flex-direction: column; }
  @media (max-width: 992px) {
    .header__langs-wrap {
      position: absolute;
      bottom: 0;
      right: 0;
      margin-right: 30px;
      transform: translateY(250%); } }

.header__lang {
  writing-mode: vertical-lr;
  text-orientation: mixed;
  padding: 5px 0;
  font-weight: bold;
  font-size: 16px;
  color: #828282;
  text-transform: uppercase; }
  @media (max-width: 992px) {
    .header__lang {
      font-size: 34px; } }
  .header__lang_active {
    color: #000000; }

.header__rus {
  border-bottom: solid #000000 4px; }

.main {
  flex-grow: 1; }

.about {
  background-color: #F6F6F6; }
  .about__inner {
    padding-top: 70px;
    padding-bottom: 65px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
    .about__inner p {
      margin: 30px 0;
      text-align: center;
      font-weight: 500;
      font-size: 18px;
      line-height: 21.83px; }

.skills__inner {
  padding-top: 70px;
  padding-bottom: 80px; }

.skills__descr {
  margin-bottom: 70px;
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  line-height: 21.83px; }

.skills__items {
  display: flex;
  justify-content: space-between; }
  @media (max-width: 992px) {
    .skills__items {
      flex-direction: column;
      align-items: center; } }

.skills__item {
  margin-bottom: 80px;
  text-align: center; }
  .skills__item-image {
    margin: 0 auto;
    max-height: 90px; }
  .skills__item-title {
    margin: 23px auto 40px;
    font-weight: bold;
    font-size: 14px;
    line-height: 17.33px;
    color: #828282;
    max-width: 80px; }
  .skills__item-stars[data-mark="5"] .skills__item-star:nth-child(-n + 4) {
    background: center/cover no-repeat url(../img/fill-star.svg); }
  .skills__item-stars[data-mark="4"] .skills__item-star:nth-child(-n + 4) {
    background: center/cover no-repeat url(../img/fill-star.svg); }
  .skills__item-stars[data-mark="3"] .skills__item-star:nth-child(-n + 3) {
    background: center/cover no-repeat url(../img/fill-star.svg); }
  .skills__item-stars[data-mark="2"] .skills__item-star:nth-child(-n + 2) {
    background: center/cover no-repeat url(../img/fill-star.svg); }
  .skills__item-stars[data-mark="1"] .skills__item-star:nth-child(-n + 1) {
    background: center/cover no-repeat url(../img/fill-star.svg); }
  .skills__item-star {
    display: inline-block;
    width: 26px;
    height: 26px;
    background: center/cover no-repeat url(../img/empty-star.svg); }

.portfolio {
  background-color: #F6F6F6; }
  .portfolio__inner {
    padding: 80px 0 40px; }
  .portfolio__title {
    margin-bottom: 65px; }
  .portfolio__items {
    display: flex; }
    @media (max-width: 992px) {
      .portfolio__items {
        flex-direction: column; } }
  .portfolio__item {
    margin-bottom: 80px;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .portfolio__item-image {
      margin-bottom: 33px; }
    .portfolio__item-link {
      color: #000000; }

.footer {
  flex-shrink: 0;
  background-color: #ffffff; }
  .footer__inner {
    padding: 80px 0; }
  .footer__descr {
    margin-bottom: 40px;
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 21.83px; }
  .footer__message {
    margin-bottom: 48px;
    padding: 12px 95px;
    display: block;
    text-align: center;
    text-decoration: none;
    color: #ffffff;
    background-color: #000000;
    border-radius: 50px;
    font-weight: bold;
    font-size: 18px;
    line-height: 22.28px;
    cursor: pointer; }
  .footer__socials-items {
    padding: 0;
    margin-bottom: 50px;
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 50px; }
  .footer__socials-descr {
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    line-height: 21.83px;
    color: #828282; }
  .footer__social-item {
    list-style-type: none;
    max-width: 40px;
    max-height: 40px; }
  .footer__social-link {
    display: block; }
